.app-works {
  padding-top: clamp(2.5rem, -2.897rem + 8.71vw, 4.938rem);
  position: relative;
  @media (max-width: 768px) {
    padding-block: 40px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 884px;
    height: 3px;
    background-color: #e4e4e4;
    @media (max-width: 1040px) {
      width: 550px;
    }
    @media (max-width: 768px) {
      width: 440px;
    }
    @media (max-width: 540px) {
      width: 300px;
    }
    @media (max-width: 540px) {
      width: 300px;
    }
    @media (max-width: 320px) {
      width: 280px;
    }
  }

  &__title {
    text-align: center;
    font-weight: 600;
    font-size: clamp(1.75rem, 0.607rem + 2.38vw, 2.75rem);
    margin-bottom: 59px;
    @media (max-width: 1040px) {
      margin-bottom: 30px;
    }
    @media (max-width: 540px) {
      font-size: 26px;
    }
  }
}
.step {
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 768px) {
      flex-direction: column;
    }
  }

  &__image {
    @media (max-width: 768px) {
      width: 200px;
      margin-bottom: 10px;
    }
  }

  &__info {
    max-width: 561px;
    @media (max-width: 768px) {
      max-width: 100%;
      width: 100%;
      margin-bottom: 10px;
    }
  }

  &__name {
    font-weight: 700;
    color: var(--accent-color);
    margin-bottom: 7px;
  }

  &__title {
    font-weight: 700;
    font-size: clamp(1.625rem, 0.625rem + 2.08vw, 2.5rem);
    line-height: 1.42;
    color: var(--title-color-first);
    margin-bottom: 17px;
    @media (max-width: 768px) {
      line-height: 1.2;
      margin-bottom: 10px;
    }
    @media (max-width: 540px) {
      font-size: 24px;
    }
  }

  &__description {
    max-width: 467px;
    color: var(--description-color);
    @media (max-width: 768px) {
      max-width: 100%;
    }
  }
}

.step--back {
  picture,
  .step__image {
    order: 4;
  }
  @media (max-width: 768px) {
    picture,
    .step__image {
      order: 0;
    }
  }
}

