.header {
  padding-bottom: 543px;
  @media (max-width: 768px) {
    padding-bottom: 393px;
  }
  @media (max-width: 540px) {
    padding-bottom: 323px;
  }
  @media (max-width: 320px) {
    padding-bottom: 283px;
  }

  &__logo {
    width: 206px;
    @media (max-width: 768px) {
      width: 150px;
    }
  }
}
.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-block: 25px 28px;
  padding-right: clamp(1.25rem, -0.962rem + 3.85vw, 2.5rem);
  padding-left: clamp(1.25rem, -6.49rem + 13.46vw, 5.625rem);
  @media (max-width: 1399px) {
    padding-block: 10px 15px;
  }

  &__list {
    display: flex;
    align-items: center;
    gap: clamp(1.875rem, -5.202rem + 12.31vw, 5.875rem);

    @media (max-width: 768px) {
      width: 0px;
      transform: translateY(-400px);
      transition: transform 0.4s ease;
      &--active {
        padding-top: 130px;
        flex-direction: column;
        gap: 5px;
        overflow-y: auto;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 20;
        width: 100%;
        height: 100vh;
        background-color: var(--accent-color);
        transform: translateY(0px);
        .nav__item {
          width: 100%;
        }
        .nav__item a {
          display: block;
          text-align: center;
          font-size: 24px;
          color: var(--second-text-color);
          padding-block: 20px;
        }
      }
    }
  }

  &__item {
    a {
      font-weight: 600;
      font-size: 16px;
      line-height: 28px;
      color: var(--main-text-color);
      transition: color 0.3 ease;
      &:hover {
        color: var(--accent-color);
      }
      @media (max-width: 768px) {
        font-size: 0;
        &:hover {
          color: var(--second-text-color);
        }
      }
    }
  }
}
.hero {
  text-align: center;
  padding-block: 73px clamp(6.25rem, -0.116rem + 10.27vw, 9.125rem);
  position: relative;
  &::before,
  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  &::before {
    background-image: url(../images/bg-1.jpg);
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
  }
  &::after {
    background-color: rgba(23, 16, 16, 0.6);
  }

  @media (max-width: 1439px) {
    padding-top: 40px;
  }

  @media (max-width: 768px) {
    padding-block: 40px 60px;
  }

  &__title {
    max-width: 960px;
    margin-inline: auto;
    font-weight: 700;
    font-size: clamp(2.375rem, 0.946rem + 2.98vw, 3.625rem);
    line-height: 1.38;
    color: var(--title-color-second);
    span {
      font-size: 24px;
      line-height: 36px;
      color: var(--second-text-color);
    }
    @media (max-width: 768px) {
      font-size: 32px;
      span {
        font-size: 18px;
      }
    }
    @media (max-width: 540px) {
      font-size: 30px;
      margin-bottom: 10px;
      span {
        font-size: 16px;
      }
    }
    @media (max-width: 320px) {
      font-size: 28px;
    }
  }

  &__text {
    margin-inline: auto;
    color: var(--second-text-color);
    margin-bottom: clamp(2rem, 0.429rem + 3.27vw, 3.375rem);
    @media (max-width: 540px) {
      margin-bottom: 25px;
    }
  }

  &__buttons {
    @media (max-width: 540px) {
      display: flex;
      flex-direction: column;
    }
  }

  &__button {
    cursor: pointer;
    background-color: transparent;
    border-radius: 30px;
    border: 1px solid var(--button-text-color);
    box-shadow: 0px 10px 30px rgba(183, 50, 39, 0.2);
    font-weight: 700;
    color: var(--button-text-color);
    padding: 16px 58px;
    transition: background-color 0.3s ease, border 0.3s ease;
    @media (any-hover: hover) {
      &:hover {
        border: 1px solid var(--accent-color);
        background-color: var(--accent-color);
      }
    }
    @media (max-width: 768px) {
      font-size: 18px;
      padding: 12px 33px;
    }
  }

  &__button:first-child {
    border: 1px solid var(--accent-color);
    background-color: var(--accent-color);
    margin-right: 34px;
    @media (any-hover: hover) {
      &:hover {
        border: 1px solid var(--accent-dark-color);
        background-color: var(--accent-dark-color);
      }
    }
    @media (max-width: 540px) {
      margin-right: 0;
      margin-bottom: 15px;
    }
  }

  &__image {
    pointer-events: none;
    position: absolute;
    z-index: 1;
    top: 60%;
    left: 42%;
    transform: translateX(-50%);
    @media (max-width: 768px) {
      width: 400px;
      top: 64%;
    }
    @media (max-width: 540px) {
      width: 350px;
      top: 75%;
    }
  }
}

.burger-menu {
  border: none;
  background-color: transparent;
  display: block;
  height: 28px;
  width: 40px;
  position: relative;
  cursor: pointer;
  z-index: 21;
  @media (min-width: 769px) {
    display: none;
  }
  span {
    position: absolute;
    top: calc(50% - 1px);
    left: 0;
    width: 100%;
    height: 3px;
    background-color: var(--main-text-color);
    transition: all 0.2s ease 0s;
    &:first-child {
      top: 0;
    }
    &:last-child {
      top: auto;
      bottom: 0;
    }
  }
  &--active {
    span {
      background-color: var(--title-color-second);
      transform: scale(0);
      &:first-child {
        transform: rotate(-45deg);
        top: calc(50% - 1px);
      }
      &:last-child {
        transform: rotate(45deg);
        bottom: calc(50% - 1px);
      }
    }
  }
}
