:root {
  --main-text-color: #252B42;
  --second-text-color: #F7F7F7;
  --title-color-first: #252B42;
  --title-color-second: #FFFFFF;
  --button-text-color: #FFFFFF;
  --description-color: #737373;
  --accent-color: #FA4A0C;
  --accent-dark-color: #DC3900;
}

html,
body {
  height: 100%;
}

.body-wrapper {
  font-family: "Montserrat", "sans-serif";
  font-style: normal;
  font-weight: 500;
  font-size: clamp(1.125rem, 0.696rem + 0.89vw, 1.5rem);
  line-height: 1.5;
  color: var(----main-text-color);
  overflow-x: hidden;
  scroll-behavior: smooth;
  min-height: 100%;
  min-width: 280px;
  max-width: 1440px;
  margin-inline: auto;
  display: flex;
  flex-direction: column;
  background: #ffffff;
  position: relative;
}

.body--lock {
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.main {
  flex: 1 1 auto;
}

.container {
  max-width: 1138px;
  padding-inline: 10px;
  margin-inline: auto;
}

.scroll-top {
  position: fixed;
  bottom: 15px;
  right: 15px;
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 50%;
  background-color: var(--accent-color);
  display: none;
  align-items: center;
  justify-content: center;
}

.scroll-top::after {
  content: "";
  pointer-events: none;
  position: absolute;
  left: 30%;
  top: 35%;
  width: 20px;
  height: 20px;
  border-top: 4px solid var(--button-text-color);
  border-right: 4px solid var(--button-text-color);
  transform: rotate(-45deg);
}

.header {
  padding-bottom: 543px;
}

.header__logo {
  width: 206px;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-block: 25px 28px;
  padding-right: clamp(1.25rem, -0.962rem + 3.85vw, 2.5rem);
  padding-left: clamp(1.25rem, -6.49rem + 13.46vw, 5.625rem);
}

.nav__list {
  display: flex;
  align-items: center;
  gap: clamp(1.875rem, -5.202rem + 12.31vw, 5.875rem);
}

.nav__item a {
  font-weight: 600;
  font-size: 16px;
  line-height: 28px;
  color: var(--main-text-color);
  transition: color 0.3 ease;
}

.nav__item a:hover {
  color: var(--accent-color);
}

.hero {
  text-align: center;
  padding-block: 73px clamp(6.25rem, -0.116rem + 10.27vw, 9.125rem);
  position: relative;
}

.hero::before,
.hero::after {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.hero::before {
  background-image: url(../images/bg-1.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}

.hero::after {
  background-color: rgba(23, 16, 16, 0.6);
}

.hero__title {
  max-width: 960px;
  margin-inline: auto;
  font-weight: 700;
  font-size: clamp(2.375rem, 0.946rem + 2.98vw, 3.625rem);
  line-height: 1.38;
  color: var(--title-color-second);
}

.hero__title span {
  font-size: 24px;
  line-height: 36px;
  color: var(--second-text-color);
}

.hero__text {
  margin-inline: auto;
  color: var(--second-text-color);
  margin-bottom: clamp(2rem, 0.429rem + 3.27vw, 3.375rem);
}

.hero__button {
  cursor: pointer;
  background-color: transparent;
  border-radius: 30px;
  border: 1px solid var(--button-text-color);
  box-shadow: 0px 10px 30px rgba(183, 50, 39, 0.2);
  font-weight: 700;
  color: var(--button-text-color);
  padding: 16px 58px;
  transition: background-color 0.3s ease, border 0.3s ease;
}

.hero__button:first-child {
  border: 1px solid var(--accent-color);
  background-color: var(--accent-color);
  margin-right: 34px;
}

.hero__image {
  pointer-events: none;
  position: absolute;
  z-index: 1;
  top: 60%;
  left: 42%;
  transform: translateX(-50%);
}

.burger-menu {
  border: none;
  background-color: transparent;
  display: block;
  height: 28px;
  width: 40px;
  position: relative;
  cursor: pointer;
  z-index: 21;
}

.burger-menu span {
  position: absolute;
  top: calc(50% - 1px);
  left: 0;
  width: 100%;
  height: 3px;
  background-color: var(--main-text-color);
  transition: all 0.2s ease 0s;
}

.burger-menu span:first-child {
  top: 0;
}

.burger-menu span:last-child {
  top: auto;
  bottom: 0;
}

.burger-menu--active span {
  background-color: var(--title-color-second);
  transform: scale(0);
}

.burger-menu--active span:first-child {
  transform: rotate(-45deg);
  top: calc(50% - 1px);
}

.burger-menu--active span:last-child {
  transform: rotate(45deg);
  bottom: calc(50% - 1px);
}

.app-works {
  padding-top: clamp(2.5rem, -2.897rem + 8.71vw, 4.938rem);
  position: relative;
}

.app-works::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 884px;
  height: 3px;
  background-color: #e4e4e4;
}

.app-works__title {
  text-align: center;
  font-weight: 600;
  font-size: clamp(1.75rem, 0.607rem + 2.38vw, 2.75rem);
  margin-bottom: 59px;
}

.step__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.step__info {
  max-width: 561px;
}

.step__name {
  font-weight: 700;
  color: var(--accent-color);
  margin-bottom: 7px;
}

.step__title {
  font-weight: 700;
  font-size: clamp(1.625rem, 0.625rem + 2.08vw, 2.5rem);
  line-height: 1.42;
  color: var(--title-color-first);
  margin-bottom: 17px;
}

.step__description {
  max-width: 467px;
  color: var(--description-color);
}

.step--back picture,
.step--back .step__image {
  order: 4;
}

.download {
  padding-block: 130px;
  position: relative;
}

.download::before,
.download::after {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.download::before {
  background-image: url(../images/bg-2.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}

.download::after {
  background-color: rgba(37, 43, 66, 0.4);
}

.download__inner {
  text-align: center;
  max-width: 840px;
  margin-inline: auto;
}

.download__title {
  font-weight: 700;
  font-size: clamp(1.75rem, 0.607rem + 2.38vw, 2.75rem);
  line-height: 1.42;
  color: var(--title-color-second);
  margin-bottom: 10px;
}

.download__text {
  color: var(--title-color-second);
  margin-bottom: 50px;
}

.download__button {
  cursor: pointer;
  background-color: transparent;
  border-radius: 10px;
  border: 1px solid var(--button-text-color);
  font-weight: 700;
  color: var(--button-text-color);
  padding: 16px 45px;
  transition: background-color 0.3s ease, border 0.3s ease;
}

.download__button:first-child {
  border: 1px solid var(--accent-color);
  background-color: var(--accent-color);
  padding: 18px 31px;
  margin-right: 16px;
}

.footer {
  padding-block: 28px;
}

.footer__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__logo {
  width: 206px;
}

.footer__copyright {
  font-family: "Roboto", "sans-serif";
  font-weight: 700;
  font-size: 12px;
  color: #5c5c5c;
}

.socials__list {
  display: flex;
  align-items: center;
  gap: 24px;
}

.socials__item svg {
  fill: var(--accent-color);
}

