@import "fonts";
@import "vars";
html,
body {
    height: 100%;
}
.body-wrapper {
    font-family: "Montserrat", "sans-serif";
    font-style: normal;
    font-weight: 500;
    font-size: clamp(1.125rem, 0.696rem + 0.89vw, 1.5rem);
    line-height: 1.5;
    color: var(----main-text-color);
    overflow-x: hidden;
    scroll-behavior: smooth;
    min-height: 100%;
    min-width: 280px;
    max-width: 1440px;
    margin-inline: auto;
    display: flex;
    flex-direction: column;
    background: #ffffff;
    position: relative;
    @media (max-width: 768px) {
        font-size: 18px;
    }
    @media (max-width: 540px) {
        font-size: 16px;
    }
}

.body--lock {
    overflow: hidden;
}

img {
    max-width: 100%;
    height: auto;
}

.main {
    flex: 1 1 auto;
}

.container {
    max-width: 1138px;
    padding-inline: 10px;
    margin-inline: auto;
}

.scroll-top {
    position: fixed;
    bottom: 15px;
    right: 15px;
    width: 50px;
    height: 50px;
    border: none;
    border-radius: 50%;
    background-color: var(--accent-color);
    display: none;
    align-items: center;
    justify-content: center;
    // @media (any-hover: hover) {
    //     &:hover {
    //         color: var(--accent-dark-color);
    //     }
    // }

    &::after {
        content: "";
        pointer-events: none;
        position: absolute;
        left: 30%;
        top: 35%;
        width: 20px;
        height: 20px;
        border-top: 4px solid var(--button-text-color);
        border-right: 4px solid var(--button-text-color);
        transform: rotate(-45deg);
    }
}
