.download {
  padding-block: 130px;
  position: relative;
  @media (max-width: 768px) {
    padding-block: 60px;
  } 
  @media (max-width: 540px) {
    padding-block: 60px 40px;
  } 

  &::before,
  &::after {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;  
    right: 0;
    bottom: 0;
  }
  &::before {
    background-image: url(../images/bg-2.jpg);
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
  }
  &::after {
    background-color:rgba(37, 43, 66, 0.4);
  }
  &__inner {
    text-align: center;
    max-width: 840px;
    margin-inline: auto;
  }
  &__title {
    font-weight: 700;
    font-size: clamp(1.75rem, 0.607rem + 2.38vw, 2.75rem);
    line-height: 1.42;
    color: var(--title-color-second);
    margin-bottom: 10px;
  }
  &__text {
    color: var(--title-color-second);
    margin-bottom: 50px;
    @media (max-width: 768px) {
      margin-bottom: 25px;
    }
  }
  &__buttons {
    @media (max-width: 540px) {
      display: flex;
      flex-direction: column;
    }
  }
  &__button {
    cursor: pointer;
    background-color: transparent;
    border-radius: 10px;
    border: 1px solid var(--button-text-color); 
    font-weight: 700;
    color: var(--button-text-color);
    padding: 16px 45px;
    transition: background-color 0.3s ease, border 0.3s ease;
    @media (any-hover: hover) {
      &:hover {
        border: 1px solid var(--accent-color);
        background-color: var(--accent-color);
      }
    }
    @media (max-width: 768px) {
      font-size: 18px;
      padding-block: 16px;
    }
  }
  &__button:first-child {
    border: 1px solid var(--accent-color);
    background-color: var(--accent-color);
    padding: 18px 31px;
    margin-right: 16px;
    @media (any-hover: hover) {
      &:hover {
        border: 1px solid var(--accent-dark-color);
        background-color: var(--accent-dark-color);
      }
    }
    @media (max-width: 768px) {
      font-size: 18px;
      padding-block: 16px;
    }
    @media (max-width: 540px) {
      margin-right: 0;
      margin-bottom: 15px;
    }
  }
}
