.footer {
  padding-block: 28px;
  @media (max-width: 540px) {
    padding-block: 12px;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 540px) {
      flex-direction: column;
      gap: 10px;
    }
  }
  
  &__logo {
    width: 206px;
    @media (max-width: 768px) {
      width: 150px;
    }
  }

  &__copyright {
    font-family: 'Roboto', 'sans-serif';
    font-weight: 700;
    font-size: 12px;
    color: #5c5c5c;
  }
}
.socials {
  &__list {
    display: flex;
    align-items: center;
    gap: 24px;
  }

  &__item {
    svg {
      fill: var(--accent-color);
      @media (any-hover: hover) {
        &:hover {
          fill: var(--accent-dark-color);
        }
      }
      @media (max-width: 768px) {
        width: 35px;
      }
    }
  }
}
